import React from "react";
import Admin from "./Admin";

const Admins = props => {
  const admins = [
    {
      name: "pazJu",
      uuid: "8f242fd6-45a7-4e88-99c3-6b39191db3c1",
      role: "Manage control",
      desc: "Współwłaściciel, Manage controller",
      disc: "paziu#4017"
    }
  ];

  return (
    <div className="container-fluid color pb-4 m-0" id="admins">
      <div className="row">
        <div className="col text-center mb-3 mt-3">
          <h2>Zarząd serwera</h2>
        </div>
      </div>
      <div className="row text-center">
        {admins.map(admin => (
          <Admin
            key={admin.name}
            uuid={admin.uuid}
            name={admin.name}
            role={admin.role}
            disc={admin.disc}
            desc={admin.desc}
            className="mb-5 mr-5 ml-5"
          />
        ))}
      </div>
    </div>
  );
};

export default Admins;
