import React from "react";
import "../App.css";

const Carousel = props => (
  <header>
    <div
      id="carouselIndicators"
      className="carousel slide"
      data-ride="carousel"
    >
      <ol className="carousel-indicators">
        <li
          data-target="#carouselIndicators"
          data-slide-to="0"
          className="active"
        ></li>
        <li data-target="#carouselIndicators" data-slide-to="1"></li>
        <li data-target="#carouselIndicators" data-slide-to="2"></li>
      </ol>

      <div className="carousel-caption">
        <h2 className="display-4">JustLegit.pl</h2>
        <p className="lead">Lepsza wersja Minecraft'a 1.8.8!</p>
        <button type="button" className="btn btn-success btn-lg">
          Jak pobrać?
        </button>
      </div>

      <div className="carousel-inner" role="listbox">
        <div className="carousel-item active imgC1"></div>

        <div className="carousel-item imgC2"></div>

        <div className="carousel-item imgC3"></div>
      </div>
      <a
        className="carousel-control-prev"
        href="#carouselIndicators"
        role="button"
        data-slide="prev"
      >
        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        <span className="sr-only">Previous</span>
      </a>
      <a
        className="carousel-control-next"
        href="#carouselIndicators"
        role="button"
        data-slide="next"
      >
        <span className="carousel-control-next-icon" aria-hidden="true"></span>
        <span className="sr-only">Next</span>
      </a>
    </div>
  </header>
);

export default Carousel;
