import React, { Component } from 'react';
import axios from 'axios';

class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      base64: null
    }
  }

  componentDidMount() {
    axios.get(`https://crafatar.com/avatars/${this.props.uuid}?size=512`, { responseType: 'arraybuffer' })
      .then((response) => {
        let image = btoa(
          new Uint8Array(response.data)
          .reduce((data, byte) => data + String.fromCharCode(byte), '')
        );
        //console.log(`data:${response.headers['content-type'].toLowerCase()};base64,${image}`);
        this.setState({base64: `data:${response.headers['content-type'].toLowerCase()};base64,${image}`});
      })
  }

  render() {
    return(
      <div className="col p-0">
        <img alt="avatar" src={this.state.base64} className="imgAdm rounded"/>
        <h1>{this.props.name}</h1>
        <h3>{this.props.role}</h3>
        <h4>{this.props.disc}</h4>
        <h5>{this.props.desc}</h5>
      </div>
    );
  }
}

export default Admin;
