import React from "react";

const Header = props => (
  <nav className="navbar abs unscrolled navbar-expand-lg navbar-dark fixed-top">
    <div className="container-fluid">
      <button
        className="navbar-toggler ml-auto"
        type="button"
        data-toggle="collapse"
        data-target="#navbarResponsive"
        aria-controls="navbarResponsive"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse rounded" id="navbarResponsive">
        <a className="navbar-brand" href="/">
          <i className="fas fa-cog mr-2"></i>
          <strong>JustLegit.pl</strong>
          <i className="fab fa-discord m-3"></i>
          <i className="fab fa-facebook m-3" />
          <i className="fab fa-youtube m-3" />
        </a>
        <ul className="navbar-nav mx-auto text-center">
          <li className="nav-item active">
            <a className="nav-link" href="/">
              Strona główna
              <span className="sr-only">(current)</span>
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#admins">
              Zarząd
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#tutorials">
              Poradniki
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">
              Bany
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#contact">
              Kontakt
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">
              Sklep
            </a>
          </li>
        </ul>
        <div className="socials">
          <i className="fab fa-discord m-3"></i>
          <i className="fab fa-facebook m-3" />
          <i className="fab fa-youtube m-3" />
        </div>
        <button type="button" className="btn btn-success">
          Jak pobrać?
        </button>
      </div>
    </div>
  </nav>
);

export default Header;
