import React from "react";
import "../App.css";

const Teaser = props => (
  <div className="container color text-center">
    <div className="row">
      <div className="col mt-4 mb-3">
        <h1>STRONA W WERSJI TESTOWEJ! NIE JEST SKOŃCZONA!</h1>
        <h2>Sprawdź nasz teaser!</h2>
      </div>
    </div>
    <div className="row">
      <div className="col">
        <div className="embed-responsive embed-responsive-16by9 mb-5">
          <iframe
            title="teaser"
            width="560"
            height="315"
            src={props.link}
            className="embed-responsive-item"
          ></iframe>
        </div>
      </div>
    </div>
  </div>
);

export default Teaser;
